import {Link, withRouter} from 'react-router-dom'
import {Row, Col} from 'antd'
import {mainConfig} from '../../../config/mainConfig'
import {useEffect, useState} from 'react'
import {Skeleton} from 'antd'
let token = ''
const query = `{
    query {
      getSupplierdata {
        code
        name
      }
    }
  }`
function SupplierWrapper(props: any) {
  const [data, setData] = useState<any>([])
  const [loading, setLoading] = useState<any>(true)
  const [error, setError] = useState<any>(null)
  useEffect(() => {
    const fetchData = async () => {
      const graphqlQuery = `
        query {
          getSupplierdata {
            code
            name
          }
        }
      `
      const graphqlEndpoint = `${mainConfig.MAIN_ENDPOINT}/graphql` // Replace with your GraphQL endpoint
      try {
        const response = await fetch(graphqlEndpoint, {
          method: 'POST',
          mode: 'cors',
          cache: 'no-cache',
          headers: {
            'Content-type': 'application/json',
            Authorization: `Bearer ${token}`,
          },
          body: JSON.stringify({query: graphqlQuery}),
        })
        const responseData = await response.json()
        if (response.ok) {
          setData(responseData.data.getSupplierdata)
        } else {
          setError(responseData.errors || 'Error fetching data')
        }
      } catch (error) {
        console.log(error, 'Error fetching data')
        setError('Error fetching data')
      } finally {
        setLoading(false)
      }
    }
    fetchData()
  }, [])
  token = JSON.parse(localStorage.getItem('portalUserDataToken') || '')
  const getPendingTask = async () => {
    await fetch(`${mainConfig.MAIN_ENDPOINT}/graphql`, {
      method: 'get',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        query: {query},
      }),
    })
      .then(function (response) {
        response.json().then(function (data) {
        })
      })
      .catch(function (err) {
        console.log('Fetch Error :-S', err)
      })
  }
  return (
    <>
      <Row>
        <Col span={20}>
          <ol
            className='breadcrumb breadcrumb-dot fs-6 fw-bold'
            style={{marginTop: '0.1%', marginBottom: '10px'}}
          >
            <Link to='/home' className='pe-1 cursor-pointer'>
              <li className='breadcrumb-item pe-1 pt-1'>Home</li>
            </Link>
            / &nbsp;<li className='breadcrumb-item pe-3'>Watch List</li>
          </ol>
          <div className='row'>
            {<Skeleton loading={loading} active></Skeleton>}
            {data.map((e: any) => {
              return (
                <div className='col-3 mt-1 mb-1 p-4 '>
                  <div className='card p-4 justify-content-center' style={{minHeight: '100px'}}>
                    {e.code}-{e.name}
                  </div>
                </div>
              )
            })}
          </div>
        </Col>
      </Row>
    </>
  )
}
export default withRouter(SupplierWrapper)
